import React from 'react'
import SectionImage from '../images/WidgetSwap.svg'

class ProfileInfo extends React.Component {
    render() {
        return (
            <div className="SectionContainer">
                <div className="InfoContainer">
                    <div className="InfoPosition">
                        <h3 className="InfoMiniHeader" id="ProfileHeader">Personalised Profiles</h3>
                        <h2 className="InfoDescription" id="LeftInfoDescription">With the power to change how your profile looks, any stat can be displayed front and center</h2>
                    </div>
                </div>
                <div className="ImageContainer" id="RightImage">
                    <img src={SectionImage} alt="Widget Movement"></img>
                </div>
            </div>
        )
    }
}

export default ProfileInfo