import React from 'react';
import StatsExpress from '../images/StatsExpress.svg'
import { Link } from 'react-router-dom'

class Footer extends React.Component {
    render() {
        return (
            <div className="Footer">
                <div>
                    <div className="FooterCopyright">
                        <Link to="/valorant"><img src={StatsExpress} alt="Stats Express Logo"></img></Link>
                        <p className="CopyrightText">
                            Copyright  2020 Stats Express. All rights reserved.
                        </p>
                    </div>
                    <div className="FooterLinks">
                        <ul>
                            <li>Links</li>
                            <li><Link to="/valorant">Homepage</Link></li>
                            <li><Link to="/valorant/leaderboards">Leaderboards</Link></li>
                            <li><Link to="/valorant/login">Log In</Link></li>
                            <li><Link to="/valorant/register">Register</Link></li>
                        </ul>
                    </div>
                </div>

                <p className="RiotDisclaimer">
                Stats Express isn't endorsed by Riot Games and doesn't reflect the views or opinions of Riot Games or anyone officially involved in producing or managing Riot Games properties. Riot Games, and all associated properties are trademarks or registered trademarks of Riot Games, Inc.
                </p>
            </div>
        );
  }
}

export default Footer;