import React from 'react'
import AgentSelect from './AgentSelect'

import Breach from '../../images/Agents/breach.svg'
import Brimstone from '../../images/Agents/Brimstone.svg'
import Cypher from '../../images/Agents/Cypher.svg'
import Jett from '../../images/Agents/Jett.svg'
import Omen from '../../images/Agents/Omen.svg'
import Phoenix from '../../images/Agents/Phoenix.svg'
import Raze from '../../images/Agents/Raze.svg'
import Reyna from '../../images/Agents/Reyna.svg'
import Sage from '../../images/Agents/Sage.svg'
import Sova from '../../images/Agents/Sova.svg'
import Viper from '../../images/Agents/Viper.svg'

import ChangeArrows from '../../images/Icons/ChangeArrows.svg'

class AgentStats extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            AgentSelected: Breach,
            AgentSelectedName: 'Breach',
            DisplaySelect: false
        }
        this.changeAgent = this.changeAgent.bind(this)
        this.openSelect = this.openSelect.bind(this)
    }
    
    createAgentList() {
        let agentImports = [Breach, Brimstone, Cypher, Jett, Omen, Phoenix, Raze, Reyna, Sage, Sova, Viper]
        let agents = [ 'Breach', 
                       'Brimstone', 
                       'Cypher', 
                       'Jett', 
                       'Omen', 
                       'Phoenix', 
                       'Raze', 
                       'Reyna', 
                       'Sage', 
                       'Sova', 
                       'Viper'
        ]
        let agentsList = agents.map((agent, index) => {
            return (<button value={index} name={agent} onClick={(e) => this.changeAgent(e)}>{agent}
                <img src={agentImports[index]} alt={agent}></img>
            </button>)
        })
        return agentsList
    }
    
    openSelect() {
        this.setState({ DisplaySelect: true })
    }

    changeAgent(e) {
        let agentImports = [Breach, Brimstone, Cypher, Jett, Omen, Phoenix, Raze, Reyna, Sage, Sova, Viper]
        
        this.setState({
            AgentSelected: agentImports[e.target.value],
            AgentSelectedName: e.target.name,
            DisplaySelect: false
        })
    }

    render() {
        return (
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Agent Stats
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <div className="Widget AgentStats">
                    {this.state.DisplaySelect ? <AgentSelect createAgentList={this.createAgentList()}/> : null}

                    <div className="AgentContainer">
                        <div className="AgentTitle">
                            {this.state.AgentSelectedName}
                            <button onClick={() => this.openSelect()}>
                                <img src={ChangeArrows} alt="Open Agent Select"></img>
                            </button>
                        </div>
                        <img src={this.state.AgentSelected} alt="Agent"></img>
                    </div>
                    <div className="ListContainer">

                    </div>
                    <div className="BarGraphContainer">

                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default AgentStats