import React from 'react'
import { withRouter } from 'react-router-dom'

class PlayerSearch extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            searchValue: ''
        }

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    
    handleChange(e) {
        this.setState({searchValue: e.target.value})
        
    }
    
    handleSubmit(event) {
        event.preventDefault()
        let queryString = this.state.searchValue
        this.props.history.push(`/valorant/player/${queryString}`)
        window.location.reload(false)
    }

    render() {
        return(
            <div className="SearchContainer">
                <form>
                    <input type="text" value={this.state.searchValue} onChange={this.handleChange} placeholder="Player name..." />
                    <input type="submit" value="Search" onClick={this.handleSubmit}/>
                </form>
            </div>
        )
    }
}

export default withRouter(PlayerSearch)