import React from 'react'

import MergedRatioComponents from './RatioComponents/MergedRatioComponents'
import MinorStats from './MinorStatComponents/MinorStats'
import CumulativeStats from './CumulativeComponents/CumulativeStats'
import BestPerformance from './AllTimeComponents/BestPerformance'
import AverageCompare from './AllTimeComponents/AverageCompare'
import AgentStats from './AgentComponents/AgentStats'
import MapStats from './MapComponents/MapStats'
import WeaponStats from './WeaponComponents/WeaponStats'


class UnrankedWidgets extends React.Component {
    render() {
        return(
            <React.Fragment>
                <BestPerformance />
                <AverageCompare />
                <AgentStats />
                <MergedRatioComponents />
                <MapStats />
                <MinorStats />
                <WeaponStats />
                <CumulativeStats />
            </React.Fragment>
        )
    }
}

export default UnrankedWidgets