import React from 'react'

class MinorStats extends React.Component {
    displayMinorStats() {
        var minorStatsArr = []
        for (let i=1; i<=5; i++) {
            minorStatsArr.push(<div className="Widget MinorStats"></div>)
        }
        return minorStatsArr
    }
    
    render() {
        return(
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Miscellaneous Stats
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <div className="MinorStatsContainer">
                    <div className="Widget MinorStats">
                        <div className="MinorStatValue">
                            45%
                        </div>
                        <div className="MinorStatTitle">
                            Headshot %
                        </div>
                    </div>
                    <div className="Widget MinorStats">
                        <div className="MinorStatValue">
                            25%
                        </div>
                        <div className="MinorStatTitle">
                            Accuracy
                        </div>
                    </div>
                    <div className="Widget MinorStats">
                        <div className="MinorStatValue">
                            420
                        </div>
                        <div className="MinorStatTitle">
                            Hours played
                        </div>
                    </div>
                    <div className="Widget MinorStats">
                        <div className="MinorStatValue">
                            150000
                        </div>
                        <div className="MinorStatTitle">
                            Total Dmg
                        </div>
                    </div>
                    <div className="Widget MinorStats">
                        <div className="MinorStatValue">
                            50
                        </div>
                        <div className="MinorStatTitle">
                            Matches Played
                        </div>
                    </div>
                </div>
            </React.Fragment>
            
        )
    }
}

export default MinorStats