import React from 'react';
import PlayerSearch from '../PlayerComponents/PlayerSearch'


class LbBody extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            contextRender: '2',
            category: 'kd',
            context: 'singleBest',
            region: 'global'
        }
        this.selectCategory = this.selectCategory.bind(this)
        this.selectContext = this.selectContext.bind(this)
        this.selectRegion = this.selectRegion.bind(this)
    }

    selectCategory(e) {
        this.setState({ contextRender: e.target.value,
                        category: e.target.name,
                        context: null })
    }
    
    selectContext(e) {
        this.setState({ context: e.target.name })
    }

    selectRegion(e) {
        this.setState({ region: e.target.name })
    }

    renderContext() {
        let value = this.state.contextRender
        if (value === '1') {
            return (
                    <React.Fragment>
                    <div className="ContextContainer">
                        <h4 className="FilterHeader">Context</h4>
                        <div className="ContextBox">
                            <button className={(this.state.context === 'singleBest') ? "ButtonFocused" : null} name="singleBest" onClick={(e) => this.selectContext(e)}>Single Game Best</button>
                            <button className={(this.state.context === 'careerAvg') ? "ButtonFocused" : null} name="careerAvg" onClick={(e) => this.selectContext(e)}>Career Average</button>
                            <button className={(this.state.context === 'careerCumul') ? "ButtonFocused" : null} name="careerCumul" onClick={(e) => this.selectContext(e)}>Career Cumulative</button>
                        </div>
                    </div>
                    </React.Fragment>  
            ) 
        } else if (value === '2') {
            return (
                <React.Fragment>
                    <div className="ContextContainer">
                        <h4 className="FilterHeader">Context</h4>
                        <div className="ContextBox">
                            <button className={(this.state.context === 'singleBest') ? "ButtonFocused" : null} name="singleBest" onClick={(e) => this.selectContext(e)}>Single Game Best</button>
                            <button className={(this.state.context === 'careerAvg') ? "ButtonFocused" : null} name="careerAvg" onClick={(e) => this.selectContext(e)}>Career Average</button>
                        </div>
                    </div>
                </React.Fragment>
            )
        } else if (value === '3') {
            return (
                <React.Fragment>
                    <div className="ContextContainer">
                        <h4 className="FilterHeader">Context</h4>
                        <div className="ContextBox">
                            <button className={(this.state.context === 'careerCumul') ? "ButtonFocused" : null} name="careerCumul" onClick={(e) => this.selectContext(e)}>Career Cumulative</button>
                        </div>
                    </div>
                </React.Fragment>
            )
        } else if (value === '4') {
            return (
                <React.Fragment>
                    <div className="ContextContainer">
                        <h4 className="FilterHeader">Context</h4>
                        <div className="ContextBox">
                            <button className={(this.state.context === 'careerBest') ? "ButtonFocused" : null} name="careerBest" onClick={(e) => this.selectContext(e)}>Career Best</button>
                            <button className={(this.state.context === 'current') ? "ButtonFocused" : null} name="current" onClick={(e) => this.selectContext(e)}>Current</button>
                        </div>
                    </div>
                </React.Fragment>
            )
        } else {
            return null
        }
    }

    createLbItems() {
        const players = [['TheMan', 5.43], ['TheMyth', 4.20], ['TheBeast', 3.65], ['Xx_Xx_xX_xX', 3.33], ['ForeverAlone(LFG)', 2.85]]

        let playerList = players.map((player, index) => {
            return (
                <div className="Widget LbItem">
                    <div className="SkewedLbNumberBox">
                        <div className="LbNumberBox">{index + 1}</div>
                    </div>
                    <div className="LbNameBox">{player[0]}</div>
                    <div className="LbValueBox">{player[1]}</div>
                </div>
            )
        })
        return playerList
    }

    render() {
        return (
            <div className="MainBody">
                <PlayerSearch />
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Leaderboards
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <div className="Widget FilterControls">
                    <div className="FilterBox">
                        <div className="CategoryContainer">
                            <h4 className="FilterHeader">Category</h4>
                            <div className="CategoryBox">
                                <button className={(this.state.category === 'kd') ? "ButtonFocused" : null} value="2" name="kd" onClick={(e) => this.selectCategory(e)}>K/D Ratio</button>
                                <button className={(this.state.category === 'kills') ? "ButtonFocused" : null} value="1" name="kills" onClick={(e) => this.selectCategory(e)}>Kills</button>
                                <button className={(this.state.category === 'rank') ? "ButtonFocused" : null} value="4" name="rank" onClick={(e) => this.selectCategory(e)}>Rank</button>
                                <button className={(this.state.category === 'assists') ? "ButtonFocused" : null} value="1" name="assists" onClick={(e) => this.selectCategory(e)}>Assists</button>
                                <button className={(this.state.category === 'combatScore') ? "ButtonFocused" : null} value="1" name="combatScore" onClick={(e) => this.selectCategory(e)}>Combat Score</button>
                                <button className={(this.state.category === 'econRating') ? "ButtonFocused" : null} value="2" name="econRating" onClick={(e) => this.selectCategory(e)}>Econ Rating</button>
                                <button className={(this.state.category === 'hsPercent') ? "ButtonFocused" : null} value="2" name="hsPercent" onClick={(e) => this.selectCategory(e)}>Headshot%</button>
                                <button className={(this.state.category === 'accuracy') ? "ButtonFocused" : null} value="2" name="accuracy" onClick={(e) => this.selectCategory(e)}>Accuracy</button>
                                <button className={(this.state.category === 'damage') ? "ButtonFocused" : null} value="1" name="damage" onClick={(e) => this.selectCategory(e)}>Damage</button>
                                <button className={(this.state.category === 'matches') ? "ButtonFocused" : null} value="3" name="matches" onClick={(e) => this.selectCategory(e)}>Matches</button>
                                <button className={(this.state.category === 'wins') ? "ButtonFocused" : null} value="3" name="wins" onClick={(e) => this.selectCategory(e)}>Wins</button>
                            </div>
                        </div>
                        {this.renderContext()}
                        <div className="RegionContainer">
                            <h4 className="FilterHeader">Region</h4>
                            <div className="RegionBox">
                                <button className={(this.state.region === 'global') ? "ButtonFocused" : null} name="global" onClick={(e) => this.selectRegion(e)} style={{ width: '100%' }}>Global</button>
                                <button className={(this.state.region === 'northAmerica') ? "ButtonFocused" : null} name="northAmerica" onClick={(e) => this.selectRegion(e)}>N. America</button>
                                <button className={(this.state.region === 'europe') ? "ButtonFocused" : null} name="europe" onClick={(e) => this.selectRegion(e)}>Europe</button>
                                <button className={(this.state.region === 'southAmerica') ? "ButtonFocused" : null} name="southAmerica" onClick={(e) => this.selectRegion(e)}>S. America</button>
                                <button className={(this.state.region === 'asia') ? "ButtonFocused" : null} name="asia" onClick={(e) => this.selectRegion(e)}>Asia</button>
                                <button className={(this.state.region === 'oceania') ? "ButtonFocused" : null} name="oceania" onClick={(e) => this.selectRegion(e)}>Oceania</button>
                                <button className={(this.state.region === 'africa') ? "ButtonFocused" : null} name="africa" onClick={(e) => this.selectRegion(e)}>Africa</button>
                            </div>
                        </div>
                    </div>
                    <div class="LbSubmitContainer">
                        <button className={(this.state.category && this.state.context && this.state.region) ? "LbSubmitActive" : null}
                                disabled={(this.state.category && this.state.context && this.state.region) ? null : true}>Submit</button>
                    </div>
                </div>
                <div id="LbItemContainer">
                    {this.createLbItems()}
                </div>
                
            </div>
        );
    }
}

export default LbBody;