import React from 'react'
import IndexHeader from '../IndexComponents/IndexHeader'
import NotFoundSVG from '../images/NotFound.svg'

class NotFound extends React.Component {
    render() {
        return(
            <div className="NotFoundApp">
                <IndexHeader />
                <div className="NotFoundContainer">
                    <img src={NotFoundSVG} alt="404 Error Graphic"></img>
                    <div className="NotFoundTextContainer">
                        <div className="NotFoundText">
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default NotFound