import React from 'react'

class CumulativeGraph extends React.Component {
    constructor(props) {
        super(props)
        this.state = {

        }
    }
    
    modifyData() {
        /*Graph height: 200px
          Graph width: 450px
          Starting point: 25, 25*/
        let GraphLines = []
        const dataSet = [[
            [0, 0.5],
            [0.111, 0.625],
            [0.222, 0.075],
            [0.333, 0.275],
            [0.444, 0.675],
            [0.555, 0.775],
            [0.666, 0.825],
            [0.777, 0.125],
            [0.888, 0.225],
            [0.999, 0.125],
        ],
        [
            [0, 0.3],
            [0.111, 0.425],
            [0.222, 0.275],
            [0.333, 0.475],
            [0.444, 0.475],
            [0.555, 0.575],
            [0.666, 0.625],
            [0.777, 0.325],
            [0.888, 0.425],
            [0.999, 0.325],
        ],
        [
            [0, 0.5],
            [0.111, 0.375],
            [0.222, 0.925],
            [0.333, 0.725],
            [0.444, 0.325],
            [0.555, 0.225],
            [0.666, 0.175],
            [0.777, 0.875],
            [0.888, 0.775],
            [0.999, 0.875],
        ],
        [
            [0, 0.1],
            [0.111, 0.2],
            [0.222, 0.4],
            [0.333, 0.8],
            [0.444, 0.1],
            [0.555, 0.2],
            [0.666, 0.4],
            [0.777, 0.8],
            [0.888, 0.4],
            [0.999, 0.2],
        ]]
        for (let i=0; i<=3; i++) {
            const svgPoints = dataSet[i].map(cords => {
                let x = cords[0] * 1000 + 50
                let y = (1 - cords[1]) * 200 + 25
                return [x, y]
            })
            GraphLines.push(svgPoints)
        }      
        return GraphLines
    }
    
    generatePath(modifiedData) {
        const smoothing = 0.16
        
        var points = modifiedData

        const line = (pointA, pointB) => {
            const lengthX = pointB[0] - pointA[0]
            const lengthY = pointB[1] - pointA[1]
            return {
                length: Math.sqrt(Math.pow(lengthX, 2) + Math.pow(lengthY, 2)),
                angle: Math.atan2(lengthY, lengthX)
            }
        }
        const controlPoint = (current, previous, next, reverse) => {

            // When 'current' is the first or last point of the array
            // 'previous' or 'next' don't exist.
            // Replace with 'current'
            const p = previous || current
            const n = next || current
          
            // Properties of the opposed-line
            const o = line(p, n)
          
            // If is end-control-point, add PI to the angle to go backward
            const angle = o.angle + (reverse ? Math.PI : 0)
            const length = o.length * smoothing
          
            // The control point position is relative to the current point
            const x = current[0] + Math.cos(angle) * length
            const y = current[1] + Math.sin(angle) * length
            return [x, y]
          }
          
          // Create the bezier curve command 
          // I:  - point (array) [x,y]: current point coordinates
          //     - i (integer): index of 'point' in the array 'a'
          //     - a (array): complete array of points coordinates
          // O:  - (string) 'C x2,y2 x1,y1 x,y': SVG cubic bezier C command
          const bezierCommand = (point, i, a) => {
          
            // start control point
            const cps = controlPoint(a[i - 1], a[i - 2], point)
          
            // end control point
            const cpe = controlPoint(point, a[i - 1], a[i + 1], true)
            return `C ${cps[0]},${cps[1]} ${cpe[0]},${cpe[1]} ${point[0]},${point[1]}`
          }
          
          // Render the svg <path> element 
          // I:  - points (array): points coordinates
          //     - command (function)
          //       I:  - point (array) [x,y]: current point coordinates
          //           - i (integer): index of 'point' in the array 'a'
          //           - a (array): complete array of points coordinates
          //       O:  - (string) a svg path command
          // O:  - (string): a Svg <path> element
          const svgPath = (points, command) => {
            // build the d attributes by looping over the points
            const d = points.reduce((acc, point, i, a) => i === 0
              ? `M${point[0]} ${point[1]}`
              : `${acc} ${command(point, i, a)}`
            , '')
            return d
          }
          return svgPath(points, bezierCommand)
          
    }

    generateLines() {
        let lines = []
        for (let i=0; i<=3; i++) {
            let blurKey = i + 10
            let lineDisplay = this.props.displayLines[Object.keys(this.props.displayLines)[i]]
            lines.push([
                <path d={`${this.generatePath(this.modifyData()[i])} V225 H50 Z`} key={`Cumulative${blurKey + 10}`} fill={lineDisplay ? `url(#GraphFill${i})` : "none"} stroke="none" />,
                <path d={this.generatePath(this.modifyData()[i])} key={`Cumulative${i}`} fill="none" strokeWidth="3px" stroke={lineDisplay ? `url(#GraphLine${i})` : "none"} />
            ])
        }  
        return lines
    }

    render() {
        return (
            <svg viewBox="0 0 1100 250">
                <defs>
                    <linearGradient id="GraphLine0" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" stopColor="#ff4655" />
                        <stop offset="100%" stopColor="#ff4655"/>
                    </linearGradient>
                    <linearGradient id="GraphLine1" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" stopColor="#ffd23c" />
                        <stop offset="100%" stopColor="#ffd23c"/>
                    </linearGradient>
                    <linearGradient id="GraphLine2" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" stopColor="#3bff6f" />
                        <stop offset="100%" stopColor="#3bff6f"/>
                    </linearGradient>
                    <linearGradient id="GraphLine3" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" stopColor="#3bbaff" />
                        <stop offset="100%" stopColor="#3bbaff"/>
                    </linearGradient>

                    <linearGradient id="GraphFill0" x1="0%" y1="0%" x2="0%" y2="100%">
                        <stop offset="0%" stopColor="#ff4655" stopOpacity="25%"/>
                        <stop offset="100%" stopColor="#ff4655" stopOpacity="0%"/>
                    </linearGradient>
                    <linearGradient id="GraphFill1" x1="0%" y1="0%" x2="0%" y2="100%">
                        <stop offset="0%" stopColor="#ffd23c" stopOpacity="25%"/>
                        <stop offset="100%" stopColor="#ffd23c" stopOpacity="0%"/>
                    </linearGradient>
                    <linearGradient id="GraphFill2" x1="0%" y1="0%" x2="0%" y2="100%">
                        <stop offset="0%" stopColor="#3bff6f" stopOpacity="25%"/>
                        <stop offset="100%" stopColor="#3bff6f" stopOpacity="00%"/>
                    </linearGradient>
                    <linearGradient id="GraphFill3" x1="0%" y1="0%" x2="0%" y2="100%">
                        <stop offset="0%" stopColor="#3bbaff" stopOpacity="25%"/>
                        <stop offset="100%" stopColor="#3bbaff" stopOpacity="00%"/>
                    </linearGradient>

                    <filter id="RankGraphLineBlur" y="-30%" height="160%">
                        <feGaussianBlur in="SourceGraphic" stdDeviation="6"/>
                    </filter>
                </defs>
                

                {this.generateLines()}
                
            </svg>
        )
    }
}
export default CumulativeGraph