import React from 'react';
import {
  BrowserRouter as Router,
  //Link,
  //useLocation,
  Switch,
  Route
} from 'react-router-dom';

import PlayerApp from './PlayerComponents/PlayerApp'
import index from './IndexComponents/IndexApp'
import NotFound from './ErrorComponents/NotFound'
import Login from './AuthComponents/Login'
import Register from './AuthComponents/Register'
import LogOut from './AuthComponents/LogOut';
import LbApp from './LeaderboardComponents/LbApp'


function App() {
  const reload = () => window.location.reload();

  return (
    <Router basename={ProcessingInstruction.env.PUBLIC_URL}>
      <Switch>
        <Route exact path="/valorant" component={index} />
        <Route exact path="/valorant/login" component={Login} />
        <Route exact path="/valorant/register" component={Register} />
        <Route exact path="/valorant/logout" component={LogOut} />
        <Route exact path="/valorant/leaderboards" component={LbApp} />
        <Route path="/valorant/player/" render={() => (<PlayerApp />)} />
        <Route path="/valorant/riot.txt" onEnter={reload}/>
        <Route path="/valorant/sitemap.txt" onEnter={reload}/>
        <Route path="*" component={NotFound} />
      </Switch>
    </Router>
  );
}

export default App;
