import React from 'react'

class Unauthorized extends React.Component {
    render() {
        return (
            <div className="Widget WidgetControl">
                <div className="WidgetLayout">
                        <div className="ControlBox">
                            <div className="ErrorCodeContainer">
                                <div className="ErrorCode">
                                    401
                                </div>
                            </div>
                            <div className="ErrorCodeContainer">
                                <div className="CodeName">
                                    Unauthorized
                                </div>
                            </div>
                        </div>
                        <div className="ErrorColumnRight">
                            <div className="ErrorMessageContainer">
                                <div className="ErrorMessage">
                                    Incorrect information was given to the server and access to this page has been blocked.
                                    If you are the owner of this page, try logging in again.
                                </div>
                            </div>
                        </div>
                </div>
            </div>
        )
    }
}

export default Unauthorized