import React from 'react'


class BestPerformance extends React.Component {
    render() {
        return(
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Best Performance
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <div className="Widget Perf">
                
                <div className="PerfContainer">
                    <div className="PerfItem">
                        <div className="PerfStat">
                            400<br></br>
                            <i>Combat Score</i>
                        </div>
                    </div>
                    <div className="PerfItemMain">
                        <div className="PerfStatMain">
                            45 / 10 / 12<br></br>
                            <i>KDA</i>
                        </div>
                    </div>
                    <div className="PerfItem">
                        <div className="PerfStat">
                            190<br></br>
                            <i>Econ Rating</i>
                        </div>
                    </div>
                </div>
            </div>
            </React.Fragment>
        )
    }
}

export default BestPerformance