import React from 'react'
import KDWidget from './KDWidget'
import WinLossWidget from './WinLossWidget'

class MergedRatioComponents extends React.Component {
    render() {
        return(
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Kill/Death + Win/Loss Ratios
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <KDWidget />
                <WinLossWidget />
            </React.Fragment>
        )
    }
}

export default MergedRatioComponents