import React from 'react'
import CurrentRank from './CurrentRank'
import RankHistory from './RankHistory'

class MergedRankComponents extends React.Component {
    render() {
        return(
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Live Rank + Rank History
                        <div className="HeaderAccent"></div>
                    </div>
                    
                </div>
                <CurrentRank />
                <RankHistory />
            </React.Fragment>
        )
    }
}

export default MergedRankComponents