import React from 'react'
import CumulativeGraph from './CumulativeGraph'

class CumulativeStats extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            display0: true,
            display1: true,
            display2: true,
            display3: true,
        };
        this.handleChange = this.handleChange.bind(this)
    }
    
    handleChange(event) {
        const target = event.target
        this.setState({
            [target.name]: target.checked
        }, () => {console.log(this.state)})
    }

    render() {
        return(
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Cumulative Stats
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <div className="Widget CumulativeStats">
                <ul className="CumulativeSubHeader">
                    <li className="CumulativeSubHeaderItem">
                        Kills
                        <label className="switch">
                            <input type="checkbox" name="display0" onChange={this.handleChange} checked={this.state.display0} />
                            <span className="slider round"></span>
                        </label>
                    </li><li className="CumulativeSubHeaderItem">
                        Deaths
                        <label className="switch">
                            <input type="checkbox" name="display1" onChange={this.handleChange} checked={this.state.display1} />
                            <span className="slider round"></span>
                        </label>
                    </li><li className="CumulativeSubHeaderItem">
                        Wins
                        <label className="switch">
                            <input type="checkbox" name="display2" onChange={this.handleChange} checked={this.state.display2} />
                            <span className="slider round"></span>
                        </label>
                    </li><li className="CumulativeSubHeaderItem">
                        Losses
                        <label className="switch">
                            <input type="checkbox" name="display3" onChange={this.handleChange} checked={this.state.display3} />
                            <span className="slider round"></span>
                        </label>
                    </li>
                </ul>
                <div className="CumulativeSvgContainer">
                    <CumulativeGraph displayLines={this.state}/>
                </div>
            </div>
            </React.Fragment>
            
        )
    }
}

export default CumulativeStats