import React from 'react'
import SectionImage from '../../images/IdCards.svg'

class SignUpMobile extends React.Component {
    render() {
        return (
            <div className="SectionContainer">
                <div className="InfoContainer">
                    <div className="InfoPosition">
                        <h3 className="InfoMiniHeader" id="SignUpHeader">No Strings Attached</h3>
                        <h2 className="InfoDescription">By using your Riot ID for identification, there's one less account you need to worry about</h2>
                    </div>
                </div>
                <div className="ImageContainer">
                    <img src={SectionImage} alt="Stats Express + Riot Games ID Cards"></img>
                </div>
            </div>
        )
    }
}

export default SignUpMobile