import React from 'react'

class Forbidden extends React.Component {
    render() {
        return (
            <div className="Widget WidgetControl">
                <div className="WidgetLayout">
                        <div className="ControlBox">
                            <div className="ErrorCodeContainer">
                                <div className="ErrorCode">
                                    403
                                </div>
                            </div>
                            <div className="ErrorCodeContainer">
                                <div className="CodeName">
                                    Forbidden
                                </div>
                            </div>
                        </div>
                        <div className="ErrorColumnRight">
                            <div className="ErrorMessageContainer">
                                <div className="ErrorMessage">
                                    Only the owner of this page is permitted to their stats.
                                    If you are the owner of this page, please log in to gain access.
                                </div>
                            </div>
                        </div>
                </div>
            </div>
        )
    }
}

export default Forbidden