import React from 'react'
import axios from 'axios'

import Unauthorized from './ErrorComponents/Unauthorized'
import Forbidden from './ErrorComponents/Forbidden'
import CustomWidgets from './CustomWidgets'
import UnrankedWidgets from './UnrankedWidgets'
import RankedWidgets from './RankedWidgets'

import PlayerSearch from './PlayerSearch'





class MainBody extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            tabSelected: 1,
            pageSelected: <CustomWidgets />,
            highlightAlign: "0%",
            playerName: '',
            WidgetArray: [true, true, true, true, true, true, true, true],
            StatusCode: null
        }
        this.tabOne = this.tabOne.bind(this)
        this.tabTwo = this.tabTwo.bind(this)
        this.tabThree = this.tabThree.bind(this)
    }
    tabOne() {
        this.setState({
            tabSelected: 1,
            highlightAlign: "0%",
            pageSelected: <CustomWidgets />
        })
    }
    tabTwo() {
        this.setState({
            tabSelected: 2,
            highlightAlign: "33.33%",
            pageSelected: <UnrankedWidgets />
        })
    }
    tabThree() {
        this.setState({
            tabSelected: 3,
            highlightAlign: "66.67%",
            pageSelected: <RankedWidgets />
        })
    }

    componentDidMount() {
        const path = window.location.pathname
        const name = path.slice(17)
        this.setState({playerName: name})

        let accessToken = localStorage.getItem('accessToken')
        let refreshToken = localStorage.getItem('refreshToken')
        let username = localStorage.getItem('username')

        axios.post('https://stats.express/api/valorant/playerAuth', {
            pageName: name,
            username: username,
            refreshToken: refreshToken
        }, {
            headers: {
                'authorization': `Bearer ${accessToken}`
            }
        }).then((res) => {
            this.setState({ StatusCode: res.status })
            if (accessToken !== res.data.accessToken) {
                localStorage.setItem('accessToken', res.data.accessToken)
            }
        })
        .catch((err) => this.setState({ StatusCode: err.response.status }))

    }
    render() {
        return(
            <div className="MainBody">
                <PlayerSearch />
                <div className="Widget PlayerBanner">
                    <div className="PlayerHeaderContainer">
                        <div className="PlayerHeader">{this.state.playerName}</div>
                    </div>
                    <div className="Widget PageTabContainer">
                        <span className="PageTabHighlight" style={{marginLeft: this.state.highlightAlign}}></span>
                        <div className={(this.state.tabSelected===1) ? "PageTab TabFocused" : "PageTab"} onClick={this.tabOne}>
                            Overview
                        </div>
                        <div className={(this.state.tabSelected===2) ? "PageTab TabFocused" : "PageTab"} onClick={this.tabTwo}>
                            Unranked
                        </div>
                        <div className={(this.state.tabSelected===3) ? "PageTab TabFocused" : "PageTab"} onClick={this.tabThree}>
                            Ranked
                        </div>
                    </div>
                </div>
                {(this.state.StatusCode === 401) ? <Unauthorized /> : null}
                {(this.state.StatusCode === 403) ? <Forbidden /> : null}                                      
                {(this.state.StatusCode === 200) ? this.state.pageSelected : null}
            </div>
        )
    }
}

export default MainBody