import MergedRatioComponents from './RatioComponents/MergedRatioComponents'
import MergedRankComponents from './RankComponents/MergedRankComponents'
import MinorStats from './MinorStatComponents/MinorStats'
import CumulativeStats from './CumulativeComponents/CumulativeStats'
import BestPerformance from './AllTimeComponents/BestPerformance'
import AverageCompare from './AllTimeComponents/AverageCompare'
import AgentStats from './AgentComponents/AgentStats'
import MapStats from './MapComponents/MapStats'
import WeaponStats from './WeaponComponents/WeaponStats'

import ReorderWidgets from './ReorderWidgets'

class CustomWidgets extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            displayReorder: false,
            WidgetArray: [true, true, true, true, true, true, true, true, true],
            initialOrder: [{
                            name: 'Rank',
                            component: <MergedRankComponents />
                          },{
                            name: 'Best Performance',
                            component: <BestPerformance />
                          },{
                            name: 'Avg Comparison',
                            component: <AverageCompare />
                          },{
                            name: 'Agent Stats',
                            component: <AgentStats />
                          },{
                            name: 'K/D + Win/Loss',
                            component: <MergedRatioComponents />
                          },{
                            name: 'Map Stats',
                            component: <MapStats />
                          },{
                            name: 'Misc Stats',
                            component: <MinorStats />
                          },{
                            name: 'Weapon Stats',
                            component: <WeaponStats />
                          },{
                            name: 'Cumulative Stats',
                            component: <CumulativeStats />
                          }],
            newOrder: [],
            currentOrder: []
        }
        this.createCurrentOrderItems = this.createCurrentOrderItems.bind(this)
        this.createNewOrderItems = this.createNewOrderItems.bind(this)
        this.reorderWidget = this.reorderWidget.bind(this)
        this.openReorder = this.openReorder.bind(this)
        this.resetReorder = this.resetReorder.bind(this)
        this.submitReorder = this.submitReorder.bind(this)
        this.closeReorder = this.closeReorder.bind(this)
    }
    
    /*changeWidgets(widgetNum) {
        let tempArray = this.state.WidgetArray
        tempArray[widgetNum] = !tempArray[widgetNum]
        this.setState({WidgetArray: tempArray}, () => console.log(this.state.WidgetArray))
    }
    selectAllWidgets() {
        let tempArray = [true, true, true, true, true, true, true, true, true]
        this.setState({WidgetArray: tempArray})
    }
    clearAllWidgets() {
        let tempArray = [false, false, false, false, false, false, false, false, false]
        this.setState({WidgetArray: tempArray})
    }*/

    reorderWidget(e) {
        
        let index = e.target.getAttribute("value")
        let parent = e.target.parentNode.getAttribute("id")
        
        if (parent === 'currentOrder') {
            let tempCurrArray = this.state.currentOrder
            let tempNewArray = this.state.newOrder
            let movedWidget = tempCurrArray[index]
            
            tempNewArray.push(movedWidget)
            tempCurrArray.splice(index, 1)

            this.setState({ currentOrder: tempCurrArray })
            this.setState({ newOrder: tempNewArray })
            console.log(this.state.currentOrder)
            console.log(this.state.initialOrder)
        } else {
            let tempCurrArray = this.state.currentOrder
            let tempNewArray = this.state.newOrder

            tempCurrArray.push(tempNewArray[index])
            tempNewArray.splice(index, 1)
            
            this.setState({ currentOrder: tempCurrArray })
            this.setState({ newOrder: tempNewArray })
        }
    }

    createCurrentOrderItems() {
        let widgets = this.state.currentOrder
        let widgetList = widgets.map((item, index) => {
            return <div className="WidgetOrderItem" value={index} onClick={(e) => this.reorderWidget(e)}>{item.name}</div>
        })
        return widgetList
    }
    createNewOrderItems() {
        let widgets = this.state.newOrder

        let widgetList = widgets.map((item, index) => {
            return <div className="WidgetOrderItem" value={index} onClick={(e) => this.reorderWidget(e)}>{item.name}</div>
        })
        return widgetList
    }

    openReorder() {
        this.setState({ displayReorder: true })
        this.setState((state) => ({ currentOrder: [...state.initialOrder] }))
    }
    resetReorder() {
        this.setState((state) => ({ currentOrder: [...state.initialOrder] }))
        this.setState({ newOrder: [] })
    }
    submitReorder() {
        if (this.state.currentOrder.length === 0) {
            this.setState((state) => ({ initialOrder: [...state.newOrder] }), () => {
                this.setState({ newOrder: [] })
            })
            this.setState({ displayReorder: false })
        } else {
            console.log('Check failed')
        }
    }
    closeReorder() {
        this.setState({ displayReorder: false })
        this.setState((state) => ({ currentOrder: [...state.initialOrder] }))
        this.setState({ newOrder: [] })
    }

    renderWidgets() {
        let widgetArray = this.state.initialOrder.map((item) => {
            return item.component
        })
        return widgetArray
    }

    render() {
        return (
            <React.Fragment>
                
                {this.state.displayReorder ? <ReorderWidgets createCurrentOrderItems={this.createCurrentOrderItems()}
                                                             createNewOrderItems={this.createNewOrderItems()}
                                                             resetReorder={this.resetReorder}
                                                             submitReorder={this.submitReorder}
                                                             closeReorder={this.closeReorder}/> : null }
                
                <div className="ReorderDisplayBox">
                    <button className="Widget ReorderDisplayButton" onClick={this.openReorder}>Reorder Widgets</button>
                </div>

                {/*<div className="TestHeaderContainer">
                        <div className="TestHeader">Test Widget
                            <div className="HeaderAccent"></div>
                        </div>
                    </div>
                <div className="TestWidget TestWidgetStats">
                    <div className="TestWidgetColor">
                        
                    </div>
        </div>*/}
                {this.renderWidgets()}
                
                
                
                {/*<div className="Widget WidgetControl">
                    <div className="WidgetLayout">
                        <div className="ControlBox">
                            <div className="ControlHeaderContainer">
                                <div className="ControlHeader">Change Page Layout</div>
                                <div className="ControlSubHeader">Select the widgets that you want to appear</div>
                            </div>
                            <div className="ControlButtonsBox">
                                <div className="ControlButtonsContainer">
                                    <button className="ControlSelectAll" onClick={() => this.selectAllWidgets()}>Select All</button>
                                    <button className="ControlClearAll" onClick={() => this.clearAllWidgets()}>Clear All</button>
                                </div>
                            </div>
                        </div>
                        <div className="ControlTableContainer">
                            <table>
                                <tbody>
                                    <tr>
                                        <td>
                                            <p>Rank</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(0)} checked={this.state.WidgetArray[0]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                        <td>
                                            <p>Best Performance</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(1)} checked={this.state.WidgetArray[1]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                    </tr>
                                    <tr>
                                    <td>
                                            <p>Average Comparison</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(2)} checked={this.state.WidgetArray[2]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                        <td>
                                            <p>Agent Stats</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(3)} checked={this.state.WidgetArray[3]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                    </tr>
                                    <tr>
                                    <td>
                                            <p>K/D + Win/Loss</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(4)} checked={this.state.WidgetArray[4]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                        <td>
                                            <p>Map Stats</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(5)} checked={this.state.WidgetArray[5]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                    </tr>
                                    <tr>
                                    <td>
                                            <p>Misc Stats</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(6)} checked={this.state.WidgetArray[6]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                        <td>
                                            <p>Weapon Stats</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(7)} checked={this.state.WidgetArray[7]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            <p>Cumulative Stats</p>
                                            <label>
                                                <input type="checkbox" onChange={() => this.changeWidgets(8)} checked={this.state.WidgetArray[8]} />
                                                <span className="widgetSlider round"></span>
                                            </label>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>*/}
                
            </React.Fragment>
        )
    }
}

export default CustomWidgets