import React from 'react'
import { Link } from 'react-router-dom'


class MobileNavPanel extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            height: 0,
            width: 0
        }
    }
    updateDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight })
    }
    componentDidMount() {
        window.addEventListener('resize', this.updateDimensions())
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions())
    }
    LoggedInButtons() {
        let username = localStorage.getItem('username')
        let userURL = `https://stats.express/valorant/player/${username}`
        
        return (<React.Fragment>
                    <a href={userURL} style={{ color: '#ff4655' }}>{username}</a>
                    <Link to="/valorant">Home Page</Link>
                    <Link to="/valorant/leaderboards">Leaderboards</Link>
                    <Link to='/valorant/logout'>Log Out</Link>
                </React.Fragment>)
    }
    LoggedOutButtons() {
        return (<React.Fragment>
                    <Link to="/valorant">Home Page</Link>
                    <Link to="/valorant/leaderboards">Leaderboards</Link>
                    <Link to='/valorant/login'>Log In</Link>
                    <Link to='/valorant/register'>Sign Up</Link>
                </React.Fragment>)
    }
    render() {
        
        return (
            <React.Fragment>
                <div className="DimBackground" style={{ width: this.state.width, height: this.state.height, top: '72px' }}></div>
                <div className="NavPanel">
                   {/*<div className="WidgetHeaderContainer" style={{ justifyContent: 'left' }}>
                        <div className="WidgetHeader" style={{ left: '5%' }}>Navigation
                            <div className="HeaderAccent"></div>
                        </div>
        </div>*/}
                    
                    {this.props.LoggedIn ? this.LoggedInButtons() : this.LoggedOutButtons()}
                    {/*<button onClick={this.props.renderNavPanel}>Close</button>*/}
                </div>
            </React.Fragment>
        )
    }
}

export default MobileNavPanel