import React from 'react'
import WeaponSelect from './WeaponSelect'

import Classic from '../../images/Guns/Classic.svg'
import Shorty from '../../images/Guns/Shorty.svg'
import Frenzy from '../../images/Guns/Frenzy.svg'
import Ghost from '../../images/Guns/Ghost.svg'
import Sheriff from '../../images/Guns/Sheriff.svg'
import Stinger from '../../images/Guns/Stinger.svg'
import Spectre from '../../images/Guns/Spectre.svg'
import Bucky from '../../images/Guns/Bucky.svg'
import Judge from '../../images/Guns/Judge.svg'
import Bulldog from '../../images/Guns/Bulldog.svg'
import Guardian from '../../images/Guns/Guardian.svg'
import Phantom from '../../images/Guns/Phantom.svg'
import Vandal from '../../images/Guns/Vandal.svg'
import Marshal from '../../images/Guns/Marshal.svg'
import Operator from '../../images/Guns/Operator.svg'
import Ares from '../../images/Guns/Ares.svg'
import Odin from '../../images/Guns/Odin.svg'
import Melee from '../../images/Guns/Melee.svg'

import ChangeArrows from '../../images/Icons/ChangeArrows.svg'

class WeaponStats extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            WeaponSelected: Classic,
            WeaponSelectedName: 'Classic',
            DisplaySelect: false
        }
        this.changeWeapon = this.changeWeapon.bind(this)
        this.openSelect = this.openSelect.bind(this)
    }
    
    createWeaponList() {
        let WeaponImports = [ 
            Classic, Shorty,
            Frenzy, Ghost,
            Sheriff, Stinger,
            Spectre, Bucky,
            Judge, Bulldog,
            Guardian, Phantom,
            Vandal, Marshal,
            Operator, Ares,
            Odin, Melee
        ]
        let weapons = [ 'Classic', 'Shorty',
                       'Frenzy', 'Ghost',
                       'Sheriff', 'Stinger',
                       'Spectre', 'Bucky',
                       'Judge', 'Bulldog',
                       'Guardian', 'Phantom',
                       'Vandal', 'Marshal',
                       'Operator', 'Ares',
                       'Odin', 'Melee'
        ]
        let weaponsList = weapons.map((weapon, index) => {
            return (<button value={index} name={weapon} onClick={(e) => this.changeWeapon(e)}>{weapon}
                        <img src={WeaponImports[index]} alt={weapon}></img>
                    </button>)
        })
        return weaponsList
    }

    openSelect() {
        this.setState({ DisplaySelect: true })
    }
    
    changeWeapon(e) {
        let WeaponImports = [ Classic, Shorty,
                              Frenzy, Ghost,
                              Sheriff, Stinger,
                              Spectre, Bucky,
                              Judge, Bulldog,
                              Guardian, Phantom,
                              Vandal, Marshal,
                              Operator, Ares,
                              Odin, Melee
        ]
        
        this.setState({
            WeaponSelected: WeaponImports[e.target.value],
            WeaponSelectedName: e.target.name,
            DisplaySelect: false
        }, () => {
            console.log(this.state)
        })
    }

    render() {
        return (
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Weapon Stats
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <div className="Widget WeaponStats">
                    {this.state.DisplaySelect ? <WeaponSelect createWeaponList={this.createWeaponList()}/> : null}

                    <div className="WeaponColumn">
                        <div className="WeaponContainer">
                            <div className="WeaponTitle">
                                {this.state.WeaponSelectedName}
                                <button onClick={() => this.openSelect()}>
                                    <img src={ChangeArrows} alt="Open Agent Select"></img>
                                </button>
                            </div>
                            <img src={this.state.WeaponSelected} alt="Weapon"></img>
                        </div>
                        <div className="WeaponStatsContainer">

                        </div>
                    </div>
                    <div className="WeaponBarGraphContainer">

                    </div>
                </div>
            </React.Fragment>
            
        )
    }
}

export default WeaponStats