import React from 'react'
import SectionImage from '../../images/Graphs.svg'

class AestheticMobile extends React.Component {
    render() {
        return (
            <div className="SectionContainer">
                <div className="InfoContainer">
                    <div className="InfoPosition">
                        <h3 className="InfoMiniHeader">Easy on the eyes</h3>
                        <h2 className="InfoDescription">With a heavy focus on design, statistics have never looked better</h2>
                    </div>
                </div>
                <div className="ImageContainer">
                    <img src={SectionImage} alt="Data and Graph Sample"></img>
                </div>
            </div>
        )
    }
}

export default AestheticMobile