import React from 'react'
import IndexHeader from '../IndexComponents/IndexHeader'
import Footer from '../IndexComponents/Footer'
import axios from 'axios'
import { withRouter } from 'react-router-dom'


class LogOut extends React.Component {
    constructor(props) {
        super(props)
        this.state = {

        }


    }
    
    componentDidMount() {
        const token = {
            refreshToken: localStorage.getItem('refreshToken')
        }
        console.log(token)
        axios.delete('https://stats.express/api/valorant/users/logout', { data: token })
        .then((res) => {
                if (res.status === 204) {
                    localStorage.removeItem('accessToken')
                    localStorage.removeItem('refreshToken')
                    localStorage.removeItem('username')
                    this.props.history.push(`/valorant`)
                } else {
                    console.log('No Response')
                }
            }).catch(err => console.log(err))
    }
    render() {
        return(
            <div className="LoginPageContainer">
                <div className="LoginPage">
                    <IndexHeader />
                    <div className="LogOutBody">
                        <div className="LogOutForm Widget">
                            <h1>Logging Out...</h1>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}
export default withRouter(LogOut)