import React from 'react'
import IndexHeader from '../IndexComponents/IndexHeader'
import Footer from '../IndexComponents/Footer'
import axios from 'axios'
import { withRouter } from 'react-router-dom'


class Login extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            email: '',
            password: '',
            buttonDisabled: true
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    
    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value }, () => {
            if (this.state.email && this.state.password) {
                this.setState({ buttonDisabled: null })
            } else {
                this.setState({ buttonDisabled: true })
            }
        })
    }

    handleSubmit(event) {
        event.preventDefault();
        const user = {
            email: this.state.email,
            password: this.state.password
        }
        console.log(user)
        axios.post('https://stats.express/api/valorant/users/login', user, {
            headers: {
                'Access-Control-Allow-Origin': '*'
            }
        }).then((res) => {
                if (res.status === 200) {
                    localStorage.setItem('accessToken', res.data.accessToken)
                    localStorage.setItem('refreshToken', res.data.refreshToken)
                    localStorage.setItem('username', res.data.username)
                    console.table({ AT: localStorage.getItem('accessToken'), RT: localStorage.getItem('refreshToken') })
                    this.props.history.push(`/valorant/player/${res.data.username}`)
                } else {
                    console.log('No Response')
                }
            }).catch(err => console.log(err))
    }

    componentDidMount() {
        let accessToken = localStorage.getItem('accessToken')
        let refreshToken = localStorage.getItem('refreshToken')
        let username = localStorage.getItem('username')

        if (accessToken || refreshToken || username) {
            this.props.history.push('/valorant')
        }
    }

    render() {
        return(
            <div className="LoginPageContainer">
                <div className="LoginPage">
                    <IndexHeader />
                    <div className="LoginBody">
                        <form className="LoginForm Widget">
                            <h1>Log In</h1>
                            <input type="email" name="email" value={this.state.email} onChange={this.handleChange} placeholder="Email"></input>
                            <input type="password" name="password" value={this.state.password} onChange={this.handleChange} placeholder="Password"></input>
                            <input type="submit" 
                                   name="submit" 
                                   value="Submit" 
                                   onClick={this.handleSubmit}
                                   style={(!this.state.buttonDisabled) ? {borderColor:'#ff4655',backgroundColor:'#ff4655',cursor:'pointer'} : {}} 
                                   disabled={this.state.buttonDisabled}></input>
                        </form>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}
export default withRouter(Login)