import React from 'react'
import KDGraph from './KDGraph'

class WinLossWidget extends React.Component {
    render() {
        return(
            <div className="Widget MainStat">
                <div className="StatHeader">60% <i>Win/Loss</i></div>
                <ul className="StatSubHeader">
                    <li className="SubHeaderItem">
                        30 <i>Wins</i>
                    </li><li className="SubHeaderItem">
                        20 <i>Losses</i>
                    </li>
                </ul>
                <div className="svgContainer">
                    <KDGraph />
                </div>
            </div>
        )
    }
}

export default WinLossWidget