import React from 'react'
import StatsExpress from '../images/StatsExpress.svg'
import { Link } from 'react-router-dom'
import LoggedInButtons from '../IndexComponents/LoggedInButtons'
import LoggedOutButtons from '../IndexComponents/LoggedOutButtons'


class IndexHeader extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            LoggedIn: null
        }
    }
    
    componentDidMount() {
        let user = localStorage.getItem('username')
        let at = localStorage.getItem('accessToken')
        let rt = localStorage.getItem('refreshToken')
        if (user && at && rt) {
            this.setState({ LoggedIn: true }, () => console.log(this.state.LoggedIn))
        } else {
            this.setState({ LoggedIn: false }, () => console.log(this.state.LoggedIn))
        }
    }

    render() {
        return(
            <nav id="IndexNavBar" style={this.props.style}>
                <div id="IndexNavBarContainer">
                    <Link to="/valorant"><img src={StatsExpress} alt="Logo"></img></Link>
                    <div className="NavButtonPos">
                        <div className="NavButtonContainer">
                            <Link to="/valorant/leaderboards" className="NavButton">Leaderboards</Link>
                            {/*<Link to="/valorant/global" className="NavButton">Global Stats</Link>*/}
                        </div>
                    </div>
                    <div className="ButtonPosContainer">
                        {this.state.LoggedIn ? <LoggedInButtons /> : <LoggedOutButtons />}
                    </div>
                </div>
		    </nav>
        )
    }
}

export default IndexHeader;