import React from 'react'
import RankIcon from '../../images/RankIcons/Radiant.svg'

class CurrentRank extends React.Component {
    render() {
        return(
            <div className="Widget CurrentRank">
                <div className="RankIconContainer">
                    <img src={RankIcon} alt="Current Rank" className="CurrentRankIcon"/>
                </div>
                <div className="RankValueContainer">
                    <div className="RankValue">RADIANT</div>
                </div>
            </div>
        )
    }
}

export default CurrentRank