import React from 'react';
import { withRouter, Link } from 'react-router-dom'
import ParallaxCharacters from '../images/ParallaxCharacters.svg'
import ParallaxForeground from '../images/ParallaxForeground.svg'


class IndexPlayerSearch extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            searchValue: ''
        }

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    
    handleChange(e) {
        this.setState({searchValue: e.target.value})
        
    }
    
    handleSubmit(event) {
        event.preventDefault()
        let queryString = this.state.searchValue
        this.props.history.push(`/valorant/player/${queryString}`)
    }

    render() {
        return(
            <div className="IndexSearchContainer">
                <img src={ParallaxCharacters} className="ParallaxCharacters" alt=""></img>
                <img src={ParallaxForeground} className="ParallaxForeground" alt=""></img>
                <div className="TitleContainer">
                    <h1 className="TitleLine1">STATS EXPRESS</h1>
                    <h1 className="TitleLine3">VALORANT</h1>
                    <div className="TitleDesc">This website is currently a working prototype. To get started:</div>
                    <Link to='/valorant/register' className="ActionButton">Sign Up Now!</Link>
                    {/*<h2 className="TitleLine2">THE WORLD'S PREMIER</h2>
                    
        <h1 className="TitleLine4">STAT TRACKING APP</h1>*/}
                </div>
                <form>
                    <input type="text" value={this.state.searchValue} onChange={this.handleChange} placeholder="Player name..." />
                    <input type="submit" value="Search" onClick={this.handleSubmit}/>
                    {/*<div>Stats Express is working non-stop to bring you the highest quality Valorant Stats resources. The stat tracking app is not available yet, however the search feature is! Type in any name to see a preview of what the tracker app will look like.</div>*/}
                </form>
            </div>
        )
    }
}

export default withRouter(IndexPlayerSearch);