import React from 'react'
import IndexHeader from '../IndexComponents/IndexHeader'
import Footer from '../IndexComponents/Footer'
import axios from 'axios'
import { withRouter } from 'react-router-dom'

class Register extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            username: '',
            email: '',
            password: '',
            pwLabel: false,
            buttonDisabled: true
        }
        this.handleChange = this.handleChange.bind(this)
        this.handlePasswordChange = this.handlePasswordChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    /* /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/ */
    
    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value }, () => {
            if (this.state.username && this.state.email && this.state.password) {
                this.setState({ buttonDisabled: null })
            } else {
                this.setState({ buttonDisabled: true })
            }
        })
    }
    
    handlePasswordChange(e) {
        this.setState({password: e.target.value}, () => {
            if (this.state.password.length < 6) {
                this.setState({ pwLabel: true })
            } else {
                this.setState({ pwLabel: false })
                
                if (this.state.username && this.state.email && this.state.password) {
                    this.setState({ buttonDisabled: null })
                } else {
                    this.setState({ buttonDisabled: true })
                }
            }
        })
        
    }
    handleSubmit(event) {
        event.preventDefault();
        const user = {
            username: this.state.username,
            email: this.state.email,
            password: this.state.password
        }
        axios.post('https://stats.express/api/valorant/users/add', user)
            .then((res) => {
                if (res) {
                    this.props.history.push('/valorant/login')
                }
            })
            .catch(err => console.log(err))
    }

    componentDidMount() {
        let accessToken = localStorage.getItem('accessToken')
        let refreshToken = localStorage.getItem('refreshToken')
        let username = localStorage.getItem('username')

        if (accessToken || refreshToken || username) {
            this.props.history.push('/valorant')
        }
    }

    render() {
        return(
            <div className="LoginPageContainer">
                <div className="LoginPage">
                    <IndexHeader />
                    <div className="LoginBody">
                        <form className="LoginForm RegisterForm Widget">
                            <h1>Sign Up</h1>
                            <input type="username" name="username" value={this.state.username} onChange={this.handleChange} placeholder="Username"></input>
                            <input type="email" name="email" value={this.state.email} onChange={this.handleChange} placeholder="Email"></input>
                            <input type="password" name="password" value={this.state.password} onChange={this.handlePasswordChange} placeholder="Password"></input>
                            {this.state.pwLabel ? <label>Password must be at least 6 characters</label> : null}
                            <input type="submit" 
                                   name="submit" 
                                   value="Submit" 
                                   onClick={this.handleSubmit}
                                   style={(!this.state.buttonDisabled) ? {borderColor:'#ff4655',backgroundColor:'#ff4655',cursor:'pointer'} : {}}
                                   disabled={this.state.buttonDisabled}></input>
                        </form>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}
export default withRouter(Register)