import React from 'react'
import redX from '../images/Icons/redX.svg'

class ReorderWidgets extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            height: 0,
            width: 0
        }
    }
    updateDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight })
    }
    componentDidMount() {
        window.addEventListener('resize', this.updateDimensions())
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions())
    }

    render() {
        return (
            <React.Fragment>
                <div className="DimBackground" style={{ width: this.state.width, height: this.state.height }}></div>
                <div className="ReorderContainer">
                    <div className="ReorderBox">
                        <div className="ElementOrderBox">
                            <div id="currentOrder" className="OrderPosBox">
                                <div className="ReorderHeader">Current Order</div>
                                {this.props.createCurrentOrderItems}
                            </div>
                        </div>
                        <div className="ReorderArrowContainer">

                        </div>
                        <div className="ElementOrderBox">
                            <div id="newOrder" className="OrderPosBox">
                                <div className="ReorderHeader">New Order</div>
                                {this.props.createNewOrderItems}
                            </div>
                        </div>
                        <div className="ReorderButtonBox">
                            <button className="ReorderReset" onClick={this.props.resetReorder}>Reset</button>
                            <button className="ReorderSubmit" onClick={this.props.submitReorder}>Submit</button>
                        </div>
                    </div>
                    <img src={redX} alt="Close tab button" className="CloseReorder" onClick={this.props.closeReorder}></img>
                </div>
            </React.Fragment>
        )
    }
}

export default ReorderWidgets