import React from 'react'
import KDGraph from './KDGraph'

class KDWidget extends React.Component {
    render() {
        return(
            <div className="Widget MainStat">
                <div className="StatHeader">1.50 <i>K/D</i></div>
                <ul className="StatSubHeader">
                    <li className="SubHeaderItem">
                        1200 <i>Kills</i>
                    </li><li className="SubHeaderItem">
                        800 <i>Deaths</i>
                    </li>
                </ul>
                <div className="svgContainer">
                    <KDGraph />
                </div>
            </div>
        )
    }
}

export default KDWidget