import React from 'react'
import MapSelect from './MapSelect'

import Ascent from '../../images/Maps/Ascent.svg'
import Bind from '../../images/Maps/Bind.svg'
import Haven from '../../images/Maps/Haven.svg'
import Split from '../../images/Maps/Split.svg'

import ChangeArrows from '../../images/Icons/ChangeArrows.svg'


class MapStats extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            MapSelected: Ascent,
            MapSelectedName: 'Ascent',
            DisplaySelect: false
        }
        this.changeMap = this.changeMap.bind(this)
        this.openSelect = this.openSelect.bind(this)
    }
    
    createMapList() {
        let MapImports = [Ascent, Bind, Haven, Split]
        let maps = [ 'Ascent', 
                       'Bind',
                       'Haven',
                       'Split'
        ]
        let mapsList = maps.map((map, index) => {
            return <button value={index} name={map} onClick={(e) => this.changeMap(e)}>{map}
                        <img src={MapImports[index]} alt={map}></img>
                   </button>
        })
        return mapsList
    }
    
    openSelect() {
        this.setState({ DisplaySelect: true })
    }

    changeMap(e) {
        let MapImports = [Ascent, Bind, Haven, Split]
        
        this.setState({
            MapSelected: MapImports[e.target.value],
            MapSelectedName: e.target.name,
            DisplaySelect: false
        }, () => {
            console.log(this.state)
        })
    }

    render() {
        return (
            <React.Fragment>
                <div className="WidgetHeaderContainer">
                    <div className="WidgetHeader">Map Stats
                        <div className="HeaderAccent"></div>
                    </div>
                </div>
                <div className="Widget MapStats">
                    {this.state.DisplaySelect ? <MapSelect createMapList={this.createMapList()}/> : null}

                    <div className="MapContainer">
                        <div className="MapTitle">
                            {this.state.MapSelectedName}
                            <button onClick={() => this.openSelect()}>
                                <img src={ChangeArrows} alt="Open Agent Select"></img>
                            </button>
                        </div>
                        <img src={this.state.MapSelected} alt="Map"></img>
                    </div>
                    <div className="ListContainer">

                    </div>
                    <div className="BarGraphContainer">

                    </div>
                </div>
            </React.Fragment>
            
        )
    }
}

export default MapStats