import React from 'react'

class KDGraph extends React.Component {
    modifyData() {
        /*Graph height: 200px
          Graph width: 450px
          Starting point: 25, 25*/
        const dataSet = [
            [0, 0.5],
            [0.111, 0.625],
            [0.222, 0.075],
            [0.333, 0.275],
            [0.444, 0.675],
            [0.555, 0.775],
            [0.666, 0.825],
            [0.777, 0.125],
            [0.888, 0.225],
            [1, 0.125]
        ]
        const svgPoints = dataSet.map(cords => {
            let x = cords[0] * 450 + 50
            let y = (1 - cords[1]) * 200
            return [x, y]
        })
        return svgPoints
    }
    
    generatePath() {
        const smoothing = 0.16
        
        var points = this.modifyData()

        /*var points = [
            [25, 125], 
            [75, 150],
            [125, 40], 
            [175, 80], 
            [225, 160],
            [275, 180], 
            [325, 190], 
            [375, 30], 
            [425, 50], 
            [475, 30]
        ]*/
        const line = (pointA, pointB) => {
            const lengthX = pointB[0] - pointA[0]
            const lengthY = pointB[1] - pointA[1]
            return {
                length: Math.sqrt(Math.pow(lengthX, 2) + Math.pow(lengthY, 2)),
                angle: Math.atan2(lengthY, lengthX)
            }
        }
        const controlPoint = (current, previous, next, reverse) => {

            // When 'current' is the first or last point of the array
            // 'previous' or 'next' don't exist.
            // Replace with 'current'
            const p = previous || current
            const n = next || current
          
            // Properties of the opposed-line
            const o = line(p, n)
          
            // If is end-control-point, add PI to the angle to go backward
            const angle = o.angle + (reverse ? Math.PI : 0)
            const length = o.length * smoothing
          
            // The control point position is relative to the current point
            const x = current[0] + Math.cos(angle) * length
            const y = current[1] + Math.sin(angle) * length
            return [x, y]
          }
          
          // Create the bezier curve command 
          // I:  - point (array) [x,y]: current point coordinates
          //     - i (integer): index of 'point' in the array 'a'
          //     - a (array): complete array of points coordinates
          // O:  - (string) 'C x2,y2 x1,y1 x,y': SVG cubic bezier C command
          const bezierCommand = (point, i, a) => {
          
            // start control point
            const cps = controlPoint(a[i - 1], a[i - 2], point)
          
            // end control point
            const cpe = controlPoint(point, a[i - 1], a[i + 1], true)
            return `C ${cps[0]},${cps[1]} ${cpe[0]},${cpe[1]} ${point[0]},${point[1]}`
          }
          
          // Render the svg <path> element 
          // I:  - points (array): points coordinates
          //     - command (function)
          //       I:  - point (array) [x,y]: current point coordinates
          //           - i (integer): index of 'point' in the array 'a'
          //           - a (array): complete array of points coordinates
          //       O:  - (string) a svg path command
          // O:  - (string): a Svg <path> element
          const svgPath = (points, command) => {
            // build the d attributes by looping over the points
            const d = points.reduce((acc, point, i, a) => i === 0
              ? `M${point[0]} ${point[1]}`
              : `${acc} ${command(point, i, a)}`
            , '')
            return d
          }
          return svgPath(points, bezierCommand)
          
    }

    render() {
        return (
            <svg viewBox="0 0 500 250">
                <defs>
                    <linearGradient id="KDGraphLine" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" stopColor="#ff5546" />
                        <stop offset="100%" stopColor="#ff4655"/>
                    </linearGradient>
                    <linearGradient id="KDGraphFill" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" stopColor="#ff5546" stopOpacity="100%"/>
                        <stop offset="100%" stopColor="#ff4655" stopOpacity="100%"/>
                    </linearGradient>
                    <linearGradient id="KDGraphFill2" x1="0%" y1="0%" x2="0%" y2="100%">
                        <stop offset="0%" stopColor="#10163c" stopOpacity="70%"/>
                        <stop offset="100%" stopColor="#10163c" stopOpacity="100%"/>
                    </linearGradient>
                    <filter id="GraphLineBlur" y="-30%" height="160%">
                        <feGaussianBlur in="SourceGraphic" stdDeviation="6"/>
                    </filter>
                </defs>
                <path d="M50 0 h450 v200 h-450 Z" fill="url(#KDGraphFill)" stroke="none"/>
                <path d="M50 0 h450 v200 h-450 Z" fill="url(#KDGraphFill2)" stroke="none"/>
                <path d={`${this.generatePath(this.modifyData())} V0 H50 Z`} fill="#10163c" stroke="none" />
                <path d={this.generatePath(this.modifyData())} fill="none" stroke="url(#KDGraphLine)" strokeWidth="3px" />
            </svg>
        )
    }
}
export default KDGraph