import React from 'react'
import StatsExpress from '../images/StatsExpress.svg'
import { Link } from 'react-router-dom'
import MobileNavPanel from './MobileNavPanel'
import PanelsIcon from '../images/Icons/Panels.svg'


class IndexMobileHeader extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            LoggedIn: null,
            PanelDisplay: false
        }
        this.renderNavPanel = this.renderNavPanel.bind(this)
    }
    
    componentDidMount() {
        let user = localStorage.getItem('username')
        let at = localStorage.getItem('accessToken')
        let rt = localStorage.getItem('refreshToken')
        if (user && at && rt) {
            this.setState({ LoggedIn: true }, () => console.log(this.state.LoggedIn))
        } else {
            this.setState({ LoggedIn: false }, () => console.log(this.state.LoggedIn))
        }
    }

    renderNavPanel() {
        this.setState((prevState) => ({ PanelDisplay: !prevState.PanelDisplay }))
    }

    render() {
        return(
            <nav id="IndexNavBar" style={this.props.style}>
                <div id="IndexNavBarContainer">
                    <Link to="/valorant"><img src={StatsExpress} alt="Logo"></img></Link>
                    
                    <div className="ButtonPosContainer">
                        <button onClick={this.renderNavPanel}>
                            <img className="PanelIcon" src={PanelsIcon} alt="Open Navigation Panel"></img>
                        </button>
                    </div>
                    {this.state.PanelDisplay ? <MobileNavPanel LoggedIn={this.state.LoggedIn} renderNavPanel={this.renderNavPanel} /> : null}
                </div>
		    </nav>
        )
    }
}

export default IndexMobileHeader;